#!/usr/bin/env node
/**
 * /zest:privacy command handler
 *
 * View and configure privacy settings for sensitive data redaction.
 *
 * Usage:
 *   /zest:privacy                          - View current settings
 *   /zest:privacy --approach=detection     - Set privacy approach
 *   /zest:privacy --aggressive=true        - Enable aggressive mode
 *   /zest:privacy --gitignore=false        - Disable .gitignore respect
 *   /zest:privacy --zest-rules=false       - Disable .zest.rules respect
 *   /zest:privacy --exclude="*.secret.js"  - Add exclusion pattern
 *   /zest:privacy --remove-exclude="*.log" - Remove exclusion pattern
 */

import {
  DEFAULT_PRIVACY_SETTINGS,
  loadSettings,
  type PrivacySettings,
  saveSettings,
} from "../config/settings.js";
import { getPrivacyManager } from "../privacy/privacy-manager.js";
import { logger } from "../utils/logger.js";

type PrivacyApproach = "detection" | "encryption" | "hybrid";

interface ParsedArgs {
  approach?: PrivacyApproach;
  aggressive?: boolean;
  gitignore?: boolean;
  zestRules?: boolean;
  exclude?: string;
  removeExclude?: string;
}

function parseArgs(args: string[]): ParsedArgs {
  const result: ParsedArgs = {};

  for (const arg of args) {
    if (arg.startsWith("--approach=")) {
      const value = arg.slice("--approach=".length);
      if (["detection", "encryption", "hybrid"].includes(value)) {
        result.approach = value as PrivacyApproach;
      }
    } else if (arg.startsWith("--aggressive=")) {
      result.aggressive = arg.slice("--aggressive=".length) === "true";
    } else if (arg.startsWith("--gitignore=")) {
      result.gitignore = arg.slice("--gitignore=".length) === "true";
    } else if (arg.startsWith("--zest-rules=")) {
      result.zestRules = arg.slice("--zest-rules=".length) === "true";
    } else if (arg.startsWith("--exclude=")) {
      result.exclude = arg.slice("--exclude=".length).replace(/^["']|["']$/g, "");
    } else if (arg.startsWith("--remove-exclude=")) {
      result.removeExclude = arg.slice("--remove-exclude=".length).replace(/^["']|["']$/g, "");
    }
  }

  return result;
}

function displaySettings(privacy: PrivacySettings): void {
  console.log("\nðŸ”’ Privacy Settings\n");

  // Approach
  const approachIcons: Record<PrivacyApproach, string> = {
    detection: "ðŸ”",
    encryption: "ðŸ”",
    hybrid: "ðŸ”„",
  };
  console.log(`${approachIcons[privacy.approach]} Approach: ${privacy.approach}`);

  // Aggressive mode
  console.log(
    `${privacy.aggressiveMode ? "âš¡" : "ðŸŒ¿"} Aggressive mode: ${privacy.aggressiveMode ? "enabled" : "disabled"}`,
  );

  // Gitignore
  console.log(
    `${privacy.enableGitignore ? "âœ…" : "âŒ"} Respect .gitignore: ${privacy.enableGitignore ? "yes" : "no"}`,
  );

  // Zest rules
  console.log(
    `${privacy.enableZestRules ? "âœ…" : "âŒ"} Respect .zest.rules: ${privacy.enableZestRules ? "yes" : "no"}`,
  );

  // Custom exclusions
  if (privacy.customExclusionPatterns.length > 0) {
    console.log(`\nðŸ“ Custom exclusion patterns (${privacy.customExclusionPatterns.length}):`);
    for (const pattern of privacy.customExclusionPatterns) {
      console.log(`   â€¢ ${pattern}`);
    }
  } else {
    console.log("\nðŸ“ Custom exclusion patterns: none");
  }

  // Show privacy manager stats if initialized
  const privacyManager = getPrivacyManager();
  const stats = privacyManager.getStats();
  if (stats) {
    console.log("\nðŸ“Š Session stats:");
    console.log(`   â€¢ Detections: ${stats.totalDetections}`);
    console.log(`   â€¢ Files processed: ${stats.filesProcessed}`);
    console.log(`   â€¢ Files excluded: ${stats.filesExcluded}`);
  }

  console.log("\nðŸ’¡ Use --help to see available options\n");
}

function displayHelp(): void {
  console.log(`
ðŸ”’ Privacy Settings Command

Usage: /zest:privacy [options]

Options:
  --approach=<value>       Set privacy approach
                           â€¢ detection  - Detect and redact sensitive data
                           â€¢ encryption - Encrypt sensitive data (AES-256)
                           â€¢ hybrid     - Encrypt highly sensitive, redact others

  --aggressive=<bool>      Enable/disable aggressive detection mode
                           â€¢ true  - More patterns, may have false positives
                           â€¢ false - Default patterns only (recommended)

  --gitignore=<bool>       Respect .gitignore for file exclusion
                           â€¢ true  - Skip files matching .gitignore patterns
                           â€¢ false - Process all files

  --zest-rules=<bool>      Respect .zest.rules for file exclusion
                           â€¢ true  - Use project .zest.rules file
                           â€¢ false - Ignore .zest.rules

  --exclude=<pattern>      Add a custom glob exclusion pattern
                           Example: --exclude="*.secret.js"

  --remove-exclude=<pat>   Remove a custom exclusion pattern
                           Example: --remove-exclude="*.log"

Examples:
  /zest:privacy                           # View current settings
  /zest:privacy --aggressive=true         # Enable aggressive detection
  /zest:privacy --exclude="secrets/**"    # Exclude secrets directory
  /zest:privacy --approach=hybrid         # Use hybrid approach
`);
}

async function main() {
  const args = process.argv.slice(2);

  // Show help
  if (args.includes("--help") || args.includes("-h")) {
    displayHelp();
    return;
  }

  try {
    const settings = await loadSettings();
    const privacy = settings.privacy ?? { ...DEFAULT_PRIVACY_SETTINGS };
    const parsedArgs = parseArgs(args);

    // If no args, just display current settings
    if (Object.keys(parsedArgs).length === 0) {
      displaySettings(privacy);
      return;
    }

    // Track what changed
    const changes: string[] = [];

    // Apply changes
    if (parsedArgs.approach !== undefined) {
      privacy.approach = parsedArgs.approach;
      changes.push(`approach â†’ ${parsedArgs.approach}`);
    }

    if (parsedArgs.aggressive !== undefined) {
      privacy.aggressiveMode = parsedArgs.aggressive;
      changes.push(`aggressive mode â†’ ${parsedArgs.aggressive ? "enabled" : "disabled"}`);
    }

    if (parsedArgs.gitignore !== undefined) {
      privacy.enableGitignore = parsedArgs.gitignore;
      changes.push(`.gitignore respect â†’ ${parsedArgs.gitignore ? "enabled" : "disabled"}`);
    }

    if (parsedArgs.zestRules !== undefined) {
      privacy.enableZestRules = parsedArgs.zestRules;
      changes.push(`.zest.rules respect â†’ ${parsedArgs.zestRules ? "enabled" : "disabled"}`);
    }

    if (parsedArgs.exclude) {
      if (!privacy.customExclusionPatterns.includes(parsedArgs.exclude)) {
        privacy.customExclusionPatterns.push(parsedArgs.exclude);
        changes.push(`added exclusion: ${parsedArgs.exclude}`);
      } else {
        console.log(`â„¹ï¸  Pattern already exists: ${parsedArgs.exclude}`);
      }
    }

    if (parsedArgs.removeExclude) {
      const index = privacy.customExclusionPatterns.indexOf(parsedArgs.removeExclude);
      if (index !== -1) {
        privacy.customExclusionPatterns.splice(index, 1);
        changes.push(`removed exclusion: ${parsedArgs.removeExclude}`);
      } else {
        console.log(`â„¹ï¸  Pattern not found: ${parsedArgs.removeExclude}`);
      }
    }

    // Save if changes were made
    if (changes.length > 0) {
      settings.privacy = privacy;
      await saveSettings(settings);

      console.log("âœ… Privacy settings updated:\n");
      for (const change of changes) {
        console.log(`   â€¢ ${change}`);
      }

      // Update privacy manager if initialized
      const privacyManager = getPrivacyManager();
      if (privacyManager.isInitialized()) {
        await privacyManager.updateSettings(privacy);
        console.log("\nðŸ”„ Privacy manager reloaded with new settings");
      }

      console.log("\nðŸ’¡ Changes take effect on the next extraction\n");
    }
  } catch (error) {
    logger.error("Failed to update privacy settings", error);
    console.error(
      "âŒ Failed to update privacy settings: " +
        (error instanceof Error ? error.message : "Unknown error"),
    );
    process.exit(1);
  }
}

main();
