/**
 * File Exclusion Service.
 *
 * Platform-agnostic service for determining which files should be excluded
 * from telemetry based on .gitignore patterns, .zest.rules, and built-in rules.
 */

import type {
  ExcludedFile,
  ExclusionReason,
  ExclusionResult,
  FileSystemAdapter,
  PrivacyConfig,
} from "../types";
import { getBuiltInPatterns } from "./built-in-rules";
import {
  createGitignoreResult,
  type GitignoreLoadResult,
  mergeGitignorePatterns,
} from "./gitignore-parser";
import { findMatchingPattern, findMatchingPatternWithBase, matchesGlob } from "./glob-matcher";
import {
  createZestRulesResult,
  mergeZestRulesPatterns,
  ZEST_RULES_FILENAME,
  type ZestRulesLoadResult,
} from "./zest-rules-parser";

/**
 * Options for creating a FileExclusionService.
 */
export interface FileExclusionServiceOptions {
  /** Privacy configuration */
  config: PrivacyConfig;
  /** File system adapter for reading files */
  fs: FileSystemAdapter;
}

/**
 * Statistics about file exclusions.
 */
export interface ExclusionStats {
  /** Total files excluded */
  totalExcluded: number;
  /** Exclusions by reason */
  byReason: Record<ExclusionReason, number>;
  /** Number of gitignore patterns loaded */
  gitignorePatternCount: number;
  /** Number of zest rules patterns loaded */
  zestRulesPatternCount: number;
  /** Number of built-in patterns */
  builtInPatternCount: number;
}

/**
 * Service for determining file exclusions.
 *
 * This service loads exclusion patterns from multiple sources:
 * - Built-in rules (always applied)
 * - .gitignore files (if enabled in config)
 * - .zest.rules files (if enabled in config)
 * - Custom patterns from config
 *
 * @example
 * ```typescript
 * const service = new FileExclusionService({
 *   config: DEFAULT_PRIVACY_CONFIG,
 *   fs: nodeFileSystemAdapter,
 * });
 *
 * await service.initialize();
 *
 * const result = await service.shouldExcludeFile("/project/.env");
 * if (result.excluded) {
 *   console.log("Excluded:", result.reason, result.matchedRule);
 * }
 * ```
 */
export class FileExclusionService {
  private config: PrivacyConfig;
  private fs: FileSystemAdapter;

  // Pattern storage
  private builtInPatterns: Set<string>;
  private gitignorePatterns: Map<string, string>; // pattern -> directory
  private zestRulesPatterns: Set<string>;
  private customPatterns: Set<string>;

  // Tracking
  private excludedFiles: Map<string, ExcludedFile>;
  private initialized: boolean;

  constructor(options: FileExclusionServiceOptions) {
    this.config = options.config;
    this.fs = options.fs;

    // Initialize pattern sets
    this.builtInPatterns = getBuiltInPatterns();
    this.gitignorePatterns = new Map();
    this.zestRulesPatterns = new Set();
    this.customPatterns = new Set(options.config.customExclusionPatterns);

    // Tracking
    this.excludedFiles = new Map();
    this.initialized = false;
  }

  /**
   * Initialize the service by loading all exclusion patterns.
   *
   * This should be called before using the service. It loads patterns
   * from .gitignore and .zest.rules files in the workspace.
   */
  async initialize(): Promise<void> {
    await this.refreshPatterns();
    this.initialized = true;
  }

  /**
   * Refresh all patterns from disk.
   *
   * Call this when files change or when config is updated.
   */
  async refreshPatterns(): Promise<void> {
    // Load gitignore patterns if enabled
    if (this.config.enableGitignore) {
      await this.loadGitignorePatterns();
    } else {
      this.gitignorePatterns.clear();
    }

    // Load zest rules patterns if enabled
    if (this.config.enableZestRules) {
      await this.loadZestRulesPatterns();
    } else {
      this.zestRulesPatterns.clear();
    }

    // Update custom patterns from config
    this.customPatterns = new Set(this.config.customExclusionPatterns);

    // Clear cached exclusions
    this.excludedFiles.clear();
  }

  /**
   * Load .gitignore patterns from workspace.
   */
  private async loadGitignorePatterns(): Promise<void> {
    this.gitignorePatterns.clear();

    const workspaceRoot = this.fs.getWorkspaceRoot();
    if (!workspaceRoot) return;

    const results: GitignoreLoadResult[] = [];

    // Load .gitignore from workspace root
    const rootGitignore = `${workspaceRoot}/.gitignore`;
    try {
      if (await this.fs.fileExists(rootGitignore)) {
        const content = await this.fs.readFile(rootGitignore);
        results.push(createGitignoreResult(rootGitignore, content));
      }
    } catch {
      // Ignore errors reading gitignore
    }

    // Merge all patterns
    this.gitignorePatterns = mergeGitignorePatterns(results);
  }

  /**
   * Load .zest.rules patterns from workspace.
   */
  private async loadZestRulesPatterns(): Promise<void> {
    this.zestRulesPatterns.clear();

    const workspaceRoot = this.fs.getWorkspaceRoot();
    if (!workspaceRoot) return;

    const results: ZestRulesLoadResult[] = [];

    // Load .zest.rules from workspace root
    const rootZestRules = `${workspaceRoot}/${ZEST_RULES_FILENAME}`;
    try {
      if (await this.fs.fileExists(rootZestRules)) {
        const content = await this.fs.readFile(rootZestRules);
        results.push(createZestRulesResult(rootZestRules, content));
      }
    } catch {
      // Ignore errors reading zest rules
    }

    // Merge all patterns
    this.zestRulesPatterns = mergeZestRulesPatterns(results);
  }

  /**
   * Update configuration and reload patterns.
   */
  async updateConfig(newConfig: Partial<PrivacyConfig>): Promise<void> {
    this.config = { ...this.config, ...newConfig };
    await this.refreshPatterns();
  }

  /**
   * Get current configuration.
   */
  getConfig(): PrivacyConfig {
    return { ...this.config };
  }

  /**
   * Check if a file should be excluded from telemetry.
   *
   * @param filePath - Absolute path to the file
   * @returns Exclusion result with reason and matched rule
   */
  shouldExcludeFile(filePath: string): ExclusionResult {
    // Check built-in patterns first (always applied, with workspace root for relative path matching)
    const workspaceRoot = this.fs.getWorkspaceRoot();
    const builtInMatch = findMatchingPattern(filePath, this.builtInPatterns, workspaceRoot);
    if (builtInMatch) {
      const result: ExclusionResult = {
        excluded: true,
        reason: "built_in_rule",
        matchedRule: builtInMatch,
      };
      this.trackExclusion(filePath, result);
      return result;
    }

    // Check gitignore patterns (if enabled)
    if (this.config.enableGitignore && this.gitignorePatterns.size > 0) {
      const gitignoreMatch = findMatchingPatternWithBase(filePath, this.gitignorePatterns);
      if (gitignoreMatch) {
        const result: ExclusionResult = {
          excluded: true,
          reason: "gitignore",
          matchedRule: gitignoreMatch.pattern,
        };
        this.trackExclusion(filePath, result);
        return result;
      }
    }

    // Check zest rules patterns (if enabled, with workspace root for relative path matching)
    if (this.config.enableZestRules && this.zestRulesPatterns.size > 0) {
      const zestMatch = findMatchingPattern(filePath, this.zestRulesPatterns, workspaceRoot);
      if (zestMatch) {
        const result: ExclusionResult = {
          excluded: true,
          reason: "zest_rules",
          matchedRule: zestMatch,
        };
        this.trackExclusion(filePath, result);
        return result;
      }
    }

    // Check custom patterns (with workspace root for relative path matching)
    if (this.customPatterns.size > 0) {
      const customMatch = findMatchingPattern(filePath, this.customPatterns, workspaceRoot);
      if (customMatch) {
        const result: ExclusionResult = {
          excluded: true,
          reason: "custom_pattern",
          matchedRule: customMatch,
        };
        this.trackExclusion(filePath, result);
        return result;
      }
    }

    // Not excluded
    return { excluded: false };
  }

  /**
   * Check if a file should be excluded based on size.
   *
   * @param fileSizeBytes - Size of the file in bytes
   * @returns True if the file exceeds the size limit
   */
  isFileTooLarge(fileSizeBytes: number): boolean {
    return fileSizeBytes > this.config.maxFileSizeBytes;
  }

  /**
   * Check if a file appears to be binary based on extension.
   *
   * @param filePath - Path to the file
   * @returns True if the file appears to be binary
   */
  isBinaryFile(filePath: string): boolean {
    // Use built-in binary patterns to check
    const binaryExtensions = [
      ".exe",
      ".dll",
      ".so",
      ".dylib",
      ".bin",
      ".obj",
      ".o",
      ".a",
      ".lib",
      ".jar",
      ".war",
      ".class",
      ".pyc",
      ".wasm",
      ".jpg",
      ".jpeg",
      ".png",
      ".gif",
      ".bmp",
      ".ico",
      ".webp",
      ".mp4",
      ".avi",
      ".mov",
      ".wmv",
      ".mkv",
      ".mp3",
      ".wav",
      ".zip",
      ".tar",
      ".gz",
      ".rar",
      ".7z",
      ".pdf",
      ".woff",
      ".woff2",
      ".ttf",
      ".otf",
      ".eot",
      ".db",
      ".sqlite",
      ".sqlite3",
    ];

    const lowerPath = filePath.toLowerCase();
    return binaryExtensions.some((ext) => lowerPath.endsWith(ext));
  }

  /**
   * Track an exclusion for statistics.
   */
  private trackExclusion(filePath: string, result: ExclusionResult): void {
    if (result.excluded && result.reason && result.matchedRule) {
      this.excludedFiles.set(filePath, {
        filePath,
        reason: result.reason,
        matchedRule: result.matchedRule,
      });
    }
  }

  /**
   * Get all tracked excluded files.
   */
  getExcludedFiles(): ExcludedFile[] {
    return Array.from(this.excludedFiles.values());
  }

  /**
   * Get exclusion statistics.
   */
  getStats(): ExclusionStats {
    const byReason: Record<ExclusionReason, number> = {
      built_in_rule: 0,
      gitignore: 0,
      zest_rules: 0,
      custom_pattern: 0,
      file_size: 0,
      binary_file: 0,
    };

    for (const excluded of this.excludedFiles.values()) {
      byReason[excluded.reason]++;
    }

    return {
      totalExcluded: this.excludedFiles.size,
      byReason,
      gitignorePatternCount: this.gitignorePatterns.size,
      zestRulesPatternCount: this.zestRulesPatterns.size,
      builtInPatternCount: this.builtInPatterns.size,
    };
  }

  /**
   * Clear tracked exclusions.
   */
  clearExclusions(): void {
    this.excludedFiles.clear();
  }

  /**
   * Clear exclusion for a specific file.
   */
  clearExclusionForFile(filePath: string): void {
    this.excludedFiles.delete(filePath);
  }

  /**
   * Check if the service is initialized.
   */
  isInitialized(): boolean {
    return this.initialized;
  }

  /**
   * Get count of patterns by source.
   */
  getPatternCounts(): {
    builtIn: number;
    gitignore: number;
    zestRules: number;
    custom: number;
    total: number;
  } {
    const counts = {
      builtIn: this.builtInPatterns.size,
      gitignore: this.gitignorePatterns.size,
      zestRules: this.zestRulesPatterns.size,
      custom: this.customPatterns.size,
      total: 0,
    };
    counts.total = counts.builtIn + counts.gitignore + counts.zestRules + counts.custom;
    return counts;
  }
}
