/**
 * User configuration management
 *
 * Handles reading and writing user settings from ~/.claude-zest/config.json
 */

import { readFile, writeFile } from "node:fs/promises";
import { dirname } from "node:path";
import { z } from "zod";
import { ensureDirectory } from "../utils/fs-utils.js";
import { logger } from "../utils/logger.js";
import { SETTINGS_FILE } from "./constants.js";

// Privacy settings schema
const PrivacySettingsSchema = z.object({
  /** How to handle detected sensitive data */
  approach: z.enum(["detection", "encryption", "hybrid"]).default("detection"),
  /** Enable aggressive detection (more patterns, more false positives) */
  aggressiveMode: z.boolean().default(false),
  /** Respect .gitignore for file exclusion */
  enableGitignore: z.boolean().default(true),
  /** Respect .zest.rules for file exclusion */
  enableZestRules: z.boolean().default(true),
  /** Additional glob patterns for file exclusion */
  customExclusionPatterns: z.array(z.string()).default([]),
});

export type PrivacySettings = z.infer<typeof PrivacySettingsSchema>;

// Zod schema for validation
const UserSettingsSchema = z.object({
  enableRemotePersistence: z.boolean(),
  excludePatterns: z.array(z.string()),
  respectGitignore: z.boolean(),
  logLevel: z.enum(["debug", "info", "warn", "error"]),
  excludedFolders: z.array(z.string()).default([]),
  privacy: PrivacySettingsSchema.optional(),
});

export type UserSettings = z.infer<typeof UserSettingsSchema>;

/** Default privacy settings */
export const DEFAULT_PRIVACY_SETTINGS: PrivacySettings = {
  approach: "detection",
  aggressiveMode: false,
  enableGitignore: true,
  enableZestRules: true,
  customExclusionPatterns: [],
};

const DEFAULT_SETTINGS: UserSettings = {
  enableRemotePersistence: true,
  excludePatterns: [],
  respectGitignore: true,
  logLevel: "info",
  excludedFolders: [],
  privacy: DEFAULT_PRIVACY_SETTINGS,
};

/**
 * Load and validate user settings from settings file
 */
export async function loadSettings(): Promise<UserSettings> {
  try {
    const content = await readFile(SETTINGS_FILE, "utf-8");
    const rawSettings = JSON.parse(content);

    // Validate with Zod schema
    const validated = UserSettingsSchema.parse(rawSettings);
    return { ...DEFAULT_SETTINGS, ...validated };
  } catch (error) {
    if (error instanceof z.ZodError) {
      logger.warn("Invalid settings format, using defaults:", error.issues);
    } else if ((error as NodeJS.ErrnoException).code !== "ENOENT") {
      // Log errors other than "file not found"
      logger.warn("Failed to load settings, using defaults:", error);
    }
    return DEFAULT_SETTINGS;
  }
}

/**
 * Save user settings to settings file
 * @throws {Error} If settings validation fails (indicates programming error)
 */
export async function saveSettings(settings: UserSettings): Promise<void> {
  // Validate settings before saving (defensive programming)
  const result = UserSettingsSchema.safeParse(settings);

  if (!result.success) {
    throw new Error(
      `Invalid settings data (this should not happen): ${JSON.stringify(result.error.issues)}`,
    );
  }

  await ensureDirectory(dirname(SETTINGS_FILE));
  await writeFile(SETTINGS_FILE, JSON.stringify(result.data, null, 2), "utf-8");
}
