/**
 * Core sensitive data detection engine.
 *
 * Platform-agnostic implementation that scans content for sensitive data patterns.
 * Does not depend on VS Code APIs or Node.js-specific modules.
 */

import { isHighlySensitivePattern, selectPatterns } from "../patterns";
import type {
  Detection,
  DetectionStats,
  PatternCategory,
  PrivacyConfig,
  SensitivePattern,
} from "../types";
import { DetectionCache, generateCacheKey } from "./cache";

/**
 * Options for creating a SensitiveDataDetector.
 */
export interface DetectorOptions {
  /** Privacy configuration */
  config: PrivacyConfig;
  /** Enable caching of scan results (default: true) */
  enableCache?: boolean;
  /** Maximum cache entries (default: 1000) */
  maxCacheEntries?: number;
  /** Cache TTL in milliseconds (default: 5 minutes) */
  cacheTtlMs?: number;
}

/**
 * Result of scanning content for sensitive data.
 */
export interface ScanResult {
  /** Whether any sensitive data was found */
  hasSensitiveData: boolean;
  /** List of detections */
  detections: Detection[];
  /** Whether result was served from cache */
  fromCache: boolean;
}

/**
 * Core sensitive data detection engine.
 *
 * Scans content for sensitive data patterns and returns detection results.
 * Supports content hashing for deduplication and overlapping detection handling.
 *
 * @example
 * ```typescript
 * const detector = new SensitiveDataDetector({
 *   config: DEFAULT_PRIVACY_CONFIG,
 * });
 *
 * const result = detector.scanContent("API_KEY=sk-123456789");
 * if (result.hasSensitiveData) {
 *   console.log("Found sensitive data:", result.detections);
 * }
 * ```
 */
export class SensitiveDataDetector {
  private patterns: SensitivePattern[];
  private config: PrivacyConfig;
  private cache: DetectionCache<ScanResult> | null;
  private stats: DetectionStats;

  constructor(options: DetectorOptions) {
    this.config = options.config;
    this.patterns = selectPatterns(options.config.aggressiveMode, options.config.enabledPatterns);

    // Initialize cache if enabled
    if (options.enableCache !== false) {
      this.cache = new DetectionCache<ScanResult>({
        maxEntries: options.maxCacheEntries ?? 1000,
        ttlMs: options.cacheTtlMs ?? 5 * 60 * 1000,
      });
    } else {
      this.cache = null;
    }

    // Initialize stats
    this.stats = this.createEmptyStats();
  }

  /**
   * Create empty detection stats.
   */
  private createEmptyStats(): DetectionStats {
    return {
      totalDetections: 0,
      byPattern: {},
      byCategory: {} as Record<PatternCategory, number>,
      contentScanned: 0,
      cacheHits: 0,
    };
  }

  /**
   * Update configuration and reload patterns.
   */
  updateConfig(newConfig: Partial<PrivacyConfig>): void {
    this.config = { ...this.config, ...newConfig };
    this.patterns = selectPatterns(this.config.aggressiveMode, this.config.enabledPatterns);
    // Clear cache when config changes as results may be different
    this.cache?.clear();
  }

  /**
   * Get current configuration.
   */
  getConfig(): PrivacyConfig {
    return { ...this.config };
  }

  /**
   * Scan content for sensitive data.
   *
   * @param content - The text content to scan
   * @param context - Optional context for cache key generation (e.g., file path)
   * @returns Scan result with detections
   */
  scanContent(content: string, context?: string): ScanResult {
    // Check cache first
    if (this.cache) {
      const cacheKey = generateCacheKey(content, context);
      const cached = this.cache.get(cacheKey);
      if (cached) {
        this.stats.cacheHits++;
        return { ...cached, fromCache: true };
      }
    }

    // Perform the scan
    const detections = this.performScan(content);

    // Deduplicate overlapping detections
    const deduplicatedDetections = this.deduplicateDetections(detections);

    // Update stats
    this.stats.contentScanned++;
    this.stats.totalDetections += deduplicatedDetections.length;
    for (const detection of deduplicatedDetections) {
      this.stats.byPattern[detection.pattern] = (this.stats.byPattern[detection.pattern] || 0) + 1;
      this.stats.byCategory[detection.category] =
        (this.stats.byCategory[detection.category] || 0) + 1;
    }

    const result: ScanResult = {
      hasSensitiveData: deduplicatedDetections.length > 0,
      detections: deduplicatedDetections,
      fromCache: false,
    };

    // Cache the result
    if (this.cache) {
      const cacheKey = generateCacheKey(content, context);
      this.cache.set(cacheKey, result);
    }

    return result;
  }

  /**
   * Perform the actual pattern matching scan.
   */
  private performScan(content: string): Detection[] {
    const detections: Detection[] = [];

    for (const pattern of this.patterns) {
      // Reset regex lastIndex for global patterns
      pattern.regex.lastIndex = 0;

      const matches = Array.from(content.matchAll(pattern.regex));

      for (const match of matches) {
        if (match.index === undefined) continue;

        // Calculate position - handle captured groups
        let start = match.index;
        let end = match.index + match[0].length;
        let matchedText = match[0];

        // If we have a captured group, use that as the actual sensitive value
        if (match[1] !== undefined) {
          const sensitiveValue = match[1];
          const sensitiveStartInMatch = match[0].indexOf(sensitiveValue);

          if (sensitiveStartInMatch !== -1) {
            start = match.index + sensitiveStartInMatch;
            end = start + sensitiveValue.length;
            matchedText = sensitiveValue;
          }
        }

        detections.push({
          pattern: pattern.name,
          description: pattern.description,
          position: { start, end },
          matchedText,
          redactionStrategy: pattern.redactionStrategy,
          category: pattern.category,
          highlySensitive: pattern.highlySensitive,
        });
      }
    }

    return detections;
  }

  /**
   * Deduplicate overlapping detections.
   *
   * When multiple patterns match the same or overlapping text, prefer:
   * 1. Higher priority patterns
   * 2. More specific patterns (e.g., aws_access_key over generic_secret)
   * 3. Earlier matches for equal priority
   */
  private deduplicateDetections(detections: Detection[]): Detection[] {
    if (detections.length <= 1) {
      return detections;
    }

    // Sort by start position, then by priority (descending)
    const sorted = [...detections].sort((a, b) => {
      if (a.position.start !== b.position.start) {
        return a.position.start - b.position.start;
      }
      // Higher priority wins when same start position
      return this.getPatternPriority(b.pattern) - this.getPatternPriority(a.pattern);
    });

    const deduplicated: Detection[] = [];

    for (const detection of sorted) {
      // Find any overlapping detection already in results
      const overlappingIndex = deduplicated.findIndex(
        (existing) =>
          detection.position.start < existing.position.end &&
          detection.position.end > existing.position.start,
      );

      if (overlappingIndex === -1) {
        // No overlap, add it
        deduplicated.push(detection);
      } else {
        // Overlap exists - compare priorities
        const existing = deduplicated[overlappingIndex];
        const existingPriority = this.getPatternPriority(existing.pattern);
        const newPriority = this.getPatternPriority(detection.pattern);

        if (newPriority > existingPriority) {
          // New detection has higher priority, replace
          deduplicated[overlappingIndex] = detection;
        }
        // Otherwise keep existing (first match wins for equal priority)
      }
    }

    return deduplicated;
  }

  /**
   * Get the priority of a pattern by name.
   */
  private getPatternPriority(patternName: string): number {
    const pattern = this.patterns.find((p) => p.name === patternName);
    return pattern?.priority ?? 50;
  }

  /**
   * Check if any detections contain highly sensitive data.
   */
  hasHighlySensitiveData(detections: Detection[]): boolean {
    return detections.some((d) => d.highlySensitive || isHighlySensitivePattern(d.pattern));
  }

  /**
   * Get detection statistics.
   */
  getStats(): DetectionStats {
    return { ...this.stats };
  }

  /**
   * Clear detection statistics.
   */
  clearStats(): void {
    this.stats = this.createEmptyStats();
  }

  /**
   * Clear the detection cache.
   */
  clearCache(): void {
    this.cache?.clear();
  }

  /**
   * Clear all data (stats and cache).
   */
  clearAll(): void {
    this.clearStats();
    this.clearCache();
  }

  /**
   * Get the number of cached entries.
   */
  getCacheSize(): number {
    return this.cache?.size ?? 0;
  }

  /**
   * Get currently active patterns.
   */
  getActivePatterns(): SensitivePattern[] {
    return [...this.patterns];
  }

  /**
   * Get the number of active patterns.
   */
  getActivePatternCount(): number {
    return this.patterns.length;
  }
}
