/**
 * Privacy Service Facade.
 *
 * Unified API that combines detection, redaction, and file exclusion
 * into a single entry point for privacy operations.
 */

import { createPrivacyConfig } from "./config/defaults";
import { type DetectorOptions, SensitiveDataDetector } from "./detection";
import { FileExclusionService, type FileExclusionServiceOptions } from "./exclusion";
import { type RedactContentOptions, redactContent } from "./redaction";
import type {
  Detection,
  ExcludedFile,
  ExclusionResult,
  FileSystemAdapter,
  PrivacyConfig,
  PrivacyStats,
  ProcessResult,
} from "./types";

/**
 * Options for creating a PrivacyService.
 */
export interface PrivacyServiceOptions {
  /** Privacy configuration (uses defaults if not provided) */
  config?: Partial<PrivacyConfig>;
  /** File system adapter for exclusion service */
  fs: FileSystemAdapter;
  /** Enable detection caching (default: true) */
  enableCache?: boolean;
  /** Maximum cache entries (default: 1000) */
  maxCacheEntries?: number;
  /** Cache TTL in milliseconds (default: 5 minutes) */
  cacheTtlMs?: number;
}

/**
 * Privacy Service - unified facade for all privacy operations.
 *
 * This service provides a single entry point for:
 * - Processing content (detection + redaction)
 * - File exclusion checks
 * - Configuration management
 * - Statistics tracking
 *
 * @example
 * ```typescript
 * const privacyService = new PrivacyService({
 *   fs: nodeFileSystemAdapter,
 *   config: { approach: "detection" },
 * });
 *
 * await privacyService.initialize();
 *
 * // Process content
 * const result = await privacyService.processContent(
 *   "API_KEY=sk-123456789",
 *   "/path/to/file.ts"
 * );
 *
 * if (result.hasSensitiveData) {
 *   console.log("Redacted:", result.content);
 * }
 *
 * // Check file exclusion
 * const excluded = await privacyService.shouldExcludeFile("/path/to/.env");
 * ```
 */
export class PrivacyService {
  private config: PrivacyConfig;
  private detector: SensitiveDataDetector;
  private exclusionService: FileExclusionService;
  private filesProcessed: number;

  constructor(options: PrivacyServiceOptions) {
    // Create full config from partial
    this.config = createPrivacyConfig(options.config);

    // Initialize detector
    const detectorOptions: DetectorOptions = {
      config: this.config,
      enableCache: options.enableCache ?? true,
      maxCacheEntries: options.maxCacheEntries,
      cacheTtlMs: options.cacheTtlMs,
    };
    this.detector = new SensitiveDataDetector(detectorOptions);

    // Initialize exclusion service
    const exclusionOptions: FileExclusionServiceOptions = {
      config: this.config,
      fs: options.fs,
    };
    this.exclusionService = new FileExclusionService(exclusionOptions);

    // Initialize counters
    this.filesProcessed = 0;
  }

  /**
   * Initialize the privacy service.
   *
   * This loads exclusion patterns from .gitignore and .zest.rules files.
   * Should be called before processing content or checking file exclusions.
   */
  async initialize(): Promise<void> {
    await this.exclusionService.initialize();
  }

  /**
   * Check if the service is initialized.
   */
  isInitialized(): boolean {
    return this.exclusionService.isInitialized();
  }

  /**
   * Process content for sensitive data detection and redaction.
   *
   * This is the main entry point for privacy processing:
   * 1. Optionally checks file exclusion (if filePath provided)
   * 2. Scans content for sensitive data patterns
   * 3. Applies appropriate redaction strategy
   *
   * @param content - The text content to process
   * @param filePath - Optional file path for context and exclusion check
   * @param options - Additional redaction options
   * @returns Processing result with redacted content and metadata
   */
  processContent(
    content: string,
    filePath?: string,
    options?: RedactContentOptions,
  ): ProcessResult {
    // Scan for sensitive data
    const scanResult = this.detector.scanContent(content, filePath);

    // If no sensitive data found, return original content
    if (!scanResult.hasSensitiveData) {
      this.filesProcessed++;
      return {
        content,
        detections: [],
        hasSensitiveData: false,
        wasRedacted: false,
      };
    }

    // Apply redaction
    const redactionResult = redactContent(content, scanResult.detections, this.config, options);

    this.filesProcessed++;

    return {
      content: redactionResult.redactedContent,
      detections: redactionResult.detections,
      hasSensitiveData: true,
      wasRedacted: redactionResult.wasRedacted,
    };
  }

  /**
   * Process content only if the file is not excluded.
   *
   * Convenience method that combines exclusion check with content processing.
   *
   * @param content - The text content to process
   * @param filePath - File path for exclusion check and context
   * @param options - Additional redaction options
   * @returns Processing result, or null if file is excluded
   */
  processContentIfNotExcluded(
    content: string,
    filePath: string,
    options?: RedactContentOptions,
  ): ProcessResult | null {
    const exclusion = this.shouldExcludeFile(filePath);
    if (exclusion.excluded) {
      return null;
    }

    return this.processContent(content, filePath, options);
  }

  /**
   * Scan content for sensitive data without redacting.
   *
   * Use this when you only need to detect sensitive data without modifying content.
   *
   * @param content - The text content to scan
   * @param context - Optional context for cache key generation
   * @returns Array of detections found
   */
  scanContent(content: string, context?: string): Detection[] {
    const result = this.detector.scanContent(content, context);
    return result.detections;
  }

  /**
   * Check if content contains sensitive data.
   *
   * @param content - The text content to check
   * @returns True if sensitive data was detected
   */
  hasSensitiveData(content: string): boolean {
    const result = this.detector.scanContent(content);
    return result.hasSensitiveData;
  }

  /**
   * Check if content contains highly sensitive data (encryption-worthy).
   *
   * @param content - The text content to check
   * @returns True if highly sensitive data was detected
   */
  hasHighlySensitiveData(content: string): boolean {
    const result = this.detector.scanContent(content);
    if (!result.hasSensitiveData) return false;
    return this.detector.hasHighlySensitiveData(result.detections);
  }

  /**
   * Check if a file should be excluded from telemetry.
   *
   * @param filePath - Absolute path to the file
   * @returns Exclusion result with reason and matched rule
   */
  shouldExcludeFile(filePath: string): ExclusionResult {
    return this.exclusionService.shouldExcludeFile(filePath);
  }

  /**
   * Check if a file should be excluded based on size.
   *
   * @param fileSizeBytes - Size of the file in bytes
   * @returns True if the file exceeds the size limit
   */
  isFileTooLarge(fileSizeBytes: number): boolean {
    return this.exclusionService.isFileTooLarge(fileSizeBytes);
  }

  /**
   * Check if a file appears to be binary based on extension.
   *
   * @param filePath - Path to the file
   * @returns True if the file appears to be binary
   */
  isBinaryFile(filePath: string): boolean {
    return this.exclusionService.isBinaryFile(filePath);
  }

  /**
   * Update privacy configuration.
   *
   * Updates both the detector and exclusion service with new settings.
   * Clears caches as results may differ with new config.
   *
   * @param partial - Partial configuration to merge with current config
   */
  async updateConfig(partial: Partial<PrivacyConfig>): Promise<void> {
    this.config = { ...this.config, ...partial };
    this.detector.updateConfig(partial);
    await this.exclusionService.updateConfig(partial);
  }

  /**
   * Get current privacy configuration.
   */
  getConfig(): PrivacyConfig {
    return { ...this.config };
  }

  /**
   * Get comprehensive privacy statistics.
   */
  getStats(): PrivacyStats {
    const detectionStats = this.detector.getStats();
    const exclusionStats = this.exclusionService.getStats();

    return {
      detection: detectionStats,
      filesExcluded: exclusionStats.totalExcluded,
      filesProcessed: this.filesProcessed,
    };
  }

  /**
   * Get all tracked excluded files.
   */
  getExcludedFiles(): ExcludedFile[] {
    return this.exclusionService.getExcludedFiles();
  }

  /**
   * Clear all cached data and statistics.
   *
   * Use this when starting a new session or when configuration changes significantly.
   */
  clearAll(): void {
    this.detector.clearAll();
    this.exclusionService.clearExclusions();
    this.filesProcessed = 0;
  }

  /**
   * Clear detection cache only.
   */
  clearCache(): void {
    this.detector.clearCache();
  }

  /**
   * Clear detection statistics only.
   */
  clearStats(): void {
    this.detector.clearStats();
    this.filesProcessed = 0;
  }

  /**
   * Clear file exclusion tracking only.
   */
  clearExclusions(): void {
    this.exclusionService.clearExclusions();
  }

  /**
   * Refresh exclusion patterns from disk.
   *
   * Call this when .gitignore or .zest.rules files change.
   */
  async refreshExclusionPatterns(): Promise<void> {
    await this.exclusionService.refreshPatterns();
  }

  /**
   * Get the number of active sensitive data patterns.
   */
  getActivePatternCount(): number {
    return this.detector.getActivePatternCount();
  }

  /**
   * Get pattern counts from the exclusion service.
   */
  getExclusionPatternCounts(): {
    builtIn: number;
    gitignore: number;
    zestRules: number;
    custom: number;
    total: number;
  } {
    return this.exclusionService.getPatternCounts();
  }

  /**
   * Get cache size from the detector.
   */
  getCacheSize(): number {
    return this.detector.getCacheSize();
  }
}
