/**
 * Zest rules file parser.
 *
 * Parses .zest.rules files which use the same syntax as .gitignore
 * but are specifically for Zest privacy exclusions.
 *
 * .zest.rules files allow users to define additional file exclusion
 * patterns beyond what's in .gitignore, specifically for Zest telemetry.
 */

import {
  type GitignoreLoadResult,
  type GitignorePattern,
  parseGitignoreContent,
} from "./gitignore-parser";

/**
 * The filename for Zest rules files.
 */
export const ZEST_RULES_FILENAME = ".zest.rules";

/**
 * Result of loading a .zest.rules file.
 */
export interface ZestRulesLoadResult {
  /** Path to the .zest.rules file */
  filePath: string;
  /** Directory containing the file */
  directory: string;
  /** Parsed patterns */
  patterns: GitignorePattern[];
  /** Just the glob patterns (no metadata) */
  globs: string[];
}

/**
 * Parse .zest.rules file content.
 *
 * Uses the same syntax as .gitignore files.
 *
 * @param content - Content of the .zest.rules file
 * @returns Array of parsed patterns
 */
export function parseZestRulesContent(content: string): GitignorePattern[] {
  // Zest rules use the same syntax as gitignore
  return parseGitignoreContent(content);
}

/**
 * Extract glob patterns from .zest.rules content.
 *
 * @param content - Content of the .zest.rules file
 * @returns Array of glob patterns (strings)
 */
export function extractZestRulesPatterns(content: string): string[] {
  const parsed = parseZestRulesContent(content);
  return parsed.filter((p) => !p.negated).map((p) => p.glob);
}

/**
 * Create a ZestRulesLoadResult from file content.
 *
 * @param filePath - Path to the .zest.rules file
 * @param content - Content of the file
 * @returns Structured result with parsed patterns
 */
export function createZestRulesResult(filePath: string, content: string): ZestRulesLoadResult {
  // Get directory by removing the filename
  const directory = filePath.replace(/[/\\][^/\\]*$/, "");

  const patterns = parseZestRulesContent(content);
  const globs = patterns.filter((p) => !p.negated).map((p) => p.glob);

  return {
    filePath,
    directory,
    patterns,
    globs,
  };
}

/**
 * Merge multiple .zest.rules results into a single pattern set.
 *
 * Unlike gitignore, .zest.rules patterns are applied globally
 * regardless of which directory they're in.
 *
 * @param results - Array of .zest.rules load results
 * @returns Set of all unique glob patterns
 */
export function mergeZestRulesPatterns(results: ZestRulesLoadResult[]): Set<string> {
  const patterns = new Set<string>();

  for (const result of results) {
    for (const glob of result.globs) {
      patterns.add(glob);
    }
  }

  return patterns;
}

/**
 * Generate example .zest.rules content.
 *
 * Useful for documentation or creating default files.
 */
export function generateExampleZestRules(): string {
  return `# Zest Privacy Rules
# This file uses the same syntax as .gitignore
# Patterns here will exclude files from Zest telemetry

# Exclude sensitive configuration
config/secrets.json
**/api-keys.json

# Exclude test fixtures with sensitive data
**/fixtures/credentials/**
**/test-data/users.json

# Exclude specific environment files
.env.production
.env.staging

# Exclude internal tools
internal/
tools/private/
`;
}
