/**
 * Gitignore file parser.
 *
 * Parses .gitignore files and converts patterns to glob format
 * suitable for the glob matcher.
 */

/**
 * Parsed gitignore pattern with metadata.
 */
export interface GitignorePattern {
  /** The original pattern from the file */
  original: string;
  /** Normalized glob pattern for matching */
  glob: string;
  /** Whether this is a negation pattern (starts with !) */
  negated: boolean;
  /** Whether this pattern only matches directories */
  directoryOnly: boolean;
}

/**
 * Parse a single gitignore line into a pattern object.
 *
 * Handles:
 * - Comments (lines starting with #)
 * - Negation patterns (starting with !)
 * - Directory-only patterns (ending with /)
 * - Rooted patterns (starting with /)
 * - Various gitignore syntax quirks
 *
 * @param line - Single line from a gitignore file
 * @returns Parsed pattern or null for empty/comment lines
 */
export function parseGitignoreLine(line: string): GitignorePattern | null {
  // Trim whitespace
  let pattern = line.trim();

  // Skip empty lines and comments
  if (!pattern || pattern.startsWith("#")) {
    return null;
  }

  // Handle trailing spaces (escaped with backslash)
  pattern = pattern.replace(/\\(\s)$/, "$1");

  // Check for negation
  const negated = pattern.startsWith("!");
  if (negated) {
    pattern = pattern.substring(1);
  }

  // Check for directory-only pattern
  const directoryOnly = pattern.endsWith("/");
  if (directoryOnly) {
    pattern = pattern.slice(0, -1);
  }

  // Store original for reference
  const original = line.trim();

  // Convert to glob pattern
  let glob = pattern;

  // Handle rooted patterns (starting with /)
  if (glob.startsWith("/")) {
    glob = glob.substring(1);
  } else if (!glob.includes("/")) {
    // Pattern without / matches in any directory
    glob = `**/${glob}`;
  }

  // Handle directory-only patterns
  if (directoryOnly) {
    glob = `${glob}/**`;
  }

  return {
    original,
    glob,
    negated,
    directoryOnly,
  };
}

/**
 * Parse a complete gitignore file content.
 *
 * @param content - Full content of a gitignore file
 * @returns Array of parsed patterns (excluding comments and empty lines)
 */
export function parseGitignoreContent(content: string): GitignorePattern[] {
  const lines = content.split("\n");
  const patterns: GitignorePattern[] = [];

  for (const line of lines) {
    const parsed = parseGitignoreLine(line);
    if (parsed) {
      patterns.push(parsed);
    }
  }

  return patterns;
}

/**
 * Extract just the glob patterns from gitignore content.
 *
 * This is a simpler interface that returns only the glob patterns
 * without metadata. Negation patterns are excluded.
 *
 * @param content - Full content of a gitignore file
 * @returns Array of glob patterns (strings)
 */
export function extractGlobPatterns(content: string): string[] {
  const parsed = parseGitignoreContent(content);

  // Filter out negation patterns and return just the globs
  return parsed.filter((p) => !p.negated).map((p) => p.glob);
}

/**
 * Result of loading a gitignore file.
 */
export interface GitignoreLoadResult {
  /** Path to the gitignore file */
  filePath: string;
  /** Directory containing the gitignore (patterns are relative to this) */
  directory: string;
  /** Parsed patterns */
  patterns: GitignorePattern[];
  /** Just the glob patterns (no metadata) */
  globs: string[];
}

/**
 * Create a GitignoreLoadResult from file content.
 *
 * @param filePath - Path to the gitignore file
 * @param content - Content of the file
 * @returns Structured result with parsed patterns
 */
export function createGitignoreResult(filePath: string, content: string): GitignoreLoadResult {
  // Get directory by removing the filename
  const directory = filePath.replace(/[/\\][^/\\]*$/, "");

  const patterns = parseGitignoreContent(content);
  const globs = patterns.filter((p) => !p.negated).map((p) => p.glob);

  return {
    filePath,
    directory,
    patterns,
    globs,
  };
}

/**
 * Merge multiple gitignore results into a pattern map.
 *
 * Returns a Map where keys are glob patterns and values are the directories
 * they apply to. This allows proper scoping of gitignore patterns.
 *
 * @param results - Array of gitignore load results
 * @returns Map of pattern -> directory
 */
export function mergeGitignorePatterns(results: GitignoreLoadResult[]): Map<string, string> {
  const patternMap = new Map<string, string>();

  for (const result of results) {
    for (const glob of result.globs) {
      // Each pattern is associated with its gitignore's directory
      patternMap.set(glob, result.directory);
    }
  }

  return patternMap;
}
