/**
 * Default configuration values for the privacy-redaction package.
 */

import type { PrivacyConfig } from "../types";

/** 10 MB default max file size */
const DEFAULT_MAX_FILE_SIZE_BYTES = 10 * 1024 * 1024;

/**
 * Default privacy configuration.
 * Provides sensible defaults for most use cases.
 */
export const DEFAULT_PRIVACY_CONFIG: PrivacyConfig = {
  approach: "detection",
  aggressiveMode: false,
  enabledPatterns: [], // Empty means use all default (non-aggressive) patterns
  enableGitignore: true,
  enableZestRules: true,
  customExclusionPatterns: [],
  maxFileSizeBytes: DEFAULT_MAX_FILE_SIZE_BYTES,
};

/**
 * Create a privacy config by merging partial config with defaults.
 */
export function createPrivacyConfig(partial?: Partial<PrivacyConfig>): PrivacyConfig {
  if (!partial) {
    return { ...DEFAULT_PRIVACY_CONFIG };
  }

  return {
    ...DEFAULT_PRIVACY_CONFIG,
    ...partial,
    // Ensure arrays are properly merged (not replaced with empty)
    enabledPatterns: partial.enabledPatterns ?? DEFAULT_PRIVACY_CONFIG.enabledPatterns,
    customExclusionPatterns:
      partial.customExclusionPatterns ?? DEFAULT_PRIVACY_CONFIG.customExclusionPatterns,
  };
}

/**
 * Validate a privacy config and return any issues.
 */
export function validatePrivacyConfig(config: PrivacyConfig): { valid: boolean; issues: string[] } {
  const issues: string[] = [];

  if (!["detection", "encryption", "hybrid"].includes(config.approach)) {
    issues.push(`Invalid approach: ${config.approach}`);
  }

  if (typeof config.aggressiveMode !== "boolean") {
    issues.push("aggressiveMode must be a boolean");
  }

  if (!Array.isArray(config.enabledPatterns)) {
    issues.push("enabledPatterns must be an array");
  }

  if (typeof config.enableGitignore !== "boolean") {
    issues.push("enableGitignore must be a boolean");
  }

  if (typeof config.enableZestRules !== "boolean") {
    issues.push("enableZestRules must be a boolean");
  }

  if (!Array.isArray(config.customExclusionPatterns)) {
    issues.push("customExclusionPatterns must be an array");
  }

  if (typeof config.maxFileSizeBytes !== "number" || config.maxFileSizeBytes <= 0) {
    issues.push("maxFileSizeBytes must be a positive number");
  }

  return {
    valid: issues.length === 0,
    issues,
  };
}
