/**
 * Glob pattern matching utilities.
 *
 * Provides platform-agnostic glob pattern matching for file exclusion.
 * Supports standard glob patterns including **, *, and ?.
 */

/**
 * Normalize path separators to forward slashes.
 * This ensures consistent matching across platforms.
 */
export function normalizePath(filePath: string): string {
  return filePath.replace(/\\/g, "/");
}

/**
 * Convert a glob pattern to a regular expression.
 *
 * Supports:
 * - `**` - matches any path segments (including none)
 * - `*` - matches anything except path separators
 * - `?` - matches single character except path separators
 * - Character classes like `[abc]`
 *
 * @param pattern - Glob pattern to convert
 * @returns Compiled regular expression
 */
export function globToRegex(pattern: string): RegExp {
  // Normalize the pattern
  let normalized = normalizePath(pattern);

  // Escape regex special characters except glob wildcards
  let regexPattern = normalized
    .replace(/[.+^${}()|[\]\\]/g, "\\$&")
    // Use placeholders for glob patterns
    .replace(/\*\*/g, "Â§DOUBLESTARÂ§")
    .replace(/\*/g, "[^/]*") // Single * matches anything except /
    .replace(/\?/g, "[^/]"); // ? matches single char except /

  // Handle ** patterns
  regexPattern = regexPattern
    .replace(/Â§DOUBLESTARÂ§\//g, "(?:.*/)?") // **/ matches any dirs including none
    .replace(/\/Â§DOUBLESTARÂ§/g, "(?:/.*)?") // /** matches any dirs including none
    .replace(/Â§DOUBLESTARÂ§/g, ".*"); // ** alone matches anything

  return new RegExp(`^${regexPattern}$`, "i");
}

/**
 * Check if a file path matches a glob pattern.
 *
 * @param filePath - File path to test (absolute or relative)
 * @param pattern - Glob pattern to match against
 * @param workspaceRoot - Optional workspace root to also test relative paths
 * @returns True if the path matches the pattern
 *
 * @example
 * ```typescript
 * matchesGlob("/project/src/index.ts", "**\/*.ts") // true
 * matchesGlob("/project/.env.local", "*.env*") // true
 * matchesGlob("/project/node_modules/pkg/index.js", "**\/node_modules\/**") // true
 * matchesGlob("/project/out/file.js", "out/**", "/project") // true (relative match)
 * ```
 */
export function matchesGlob(filePath: string, pattern: string, workspaceRoot?: string): boolean {
  const normalizedPath = normalizePath(filePath);
  const regex = globToRegex(pattern);

  // Test the full path
  if (regex.test(normalizedPath)) {
    return true;
  }

  // Also test the filename alone for simple patterns like "*.env*"
  if (!pattern.includes("/")) {
    const fileName = normalizedPath.split("/").pop() || "";
    if (regex.test(fileName)) {
      return true;
    }
  }

  // If workspace root provided, also test relative path
  // This allows patterns like "out/**" to match absolute paths
  if (workspaceRoot) {
    const normalizedRoot = normalizePath(workspaceRoot);
    if (normalizedPath.startsWith(normalizedRoot)) {
      let relativePath = normalizedPath.substring(normalizedRoot.length);
      if (relativePath.startsWith("/")) {
        relativePath = relativePath.substring(1);
      }
      if (regex.test(relativePath)) {
        return true;
      }
    }
  }

  return false;
}

/**
 * Check if a path matches against the pattern, considering a base directory.
 *
 * This is useful for gitignore patterns that are relative to the gitignore location.
 *
 * @param filePath - Absolute file path
 * @param pattern - Glob pattern
 * @param baseDir - Base directory to resolve relative paths from
 * @returns True if the path matches
 */
export function matchesGlobRelative(filePath: string, pattern: string, baseDir: string): boolean {
  const normalizedPath = normalizePath(filePath);
  const normalizedBase = normalizePath(baseDir);

  // Check if file is under the base directory
  if (!normalizedPath.startsWith(normalizedBase)) {
    return false;
  }

  // Get relative path from base
  let relativePath = normalizedPath.substring(normalizedBase.length);
  if (relativePath.startsWith("/")) {
    relativePath = relativePath.substring(1);
  }

  // Test against the pattern
  return matchesGlob(relativePath, pattern);
}

/**
 * Find the first matching pattern from a set of patterns.
 *
 * @param filePath - File path to test
 * @param patterns - Set or array of patterns to test against
 * @param workspaceRoot - Optional workspace root for relative path matching
 * @returns The matching pattern, or null if none match
 */
export function findMatchingPattern(
  filePath: string,
  patterns: Set<string> | string[],
  workspaceRoot?: string,
): string | null {
  for (const pattern of patterns) {
    if (matchesGlob(filePath, pattern, workspaceRoot)) {
      return pattern;
    }
  }
  return null;
}

/**
 * Find the first matching pattern with base directory context.
 *
 * @param filePath - Absolute file path
 * @param patternMap - Map of patterns to their base directories
 * @returns Object with pattern and baseDir, or null if no match
 */
export function findMatchingPatternWithBase(
  filePath: string,
  patternMap: Map<string, string>,
): { pattern: string; baseDir: string } | null {
  for (const [pattern, baseDir] of patternMap) {
    if (matchesGlobRelative(filePath, pattern, baseDir)) {
      return { pattern, baseDir };
    }
  }
  return null;
}

/**
 * Check if a path is under a given directory.
 */
export function isPathUnder(filePath: string, directory: string): boolean {
  const normalizedPath = normalizePath(filePath);
  const normalizedDir = normalizePath(directory);

  // Ensure directory ends with /
  const dirWithSlash = normalizedDir.endsWith("/") ? normalizedDir : `${normalizedDir}/`;

  return normalizedPath.startsWith(dirWithSlash) || normalizedPath === normalizedDir;
}

/**
 * Get relative path from a base directory.
 */
export function getRelativePath(filePath: string, baseDir: string): string {
  const normalizedPath = normalizePath(filePath);
  const normalizedBase = normalizePath(baseDir);

  if (!normalizedPath.startsWith(normalizedBase)) {
    return normalizedPath;
  }

  let relative = normalizedPath.substring(normalizedBase.length);
  if (relative.startsWith("/")) {
    relative = relative.substring(1);
  }

  return relative;
}
