/**
 * Content caching utilities for avoiding redundant scans.
 *
 * Provides a simple hash-based cache to track previously scanned content
 * and avoid re-scanning identical strings.
 */

/**
 * Options for the detection cache.
 */
export interface DetectionCacheOptions {
  /** Maximum number of entries to keep in the cache (default: 1000) */
  maxEntries?: number;
  /** Time-to-live for cache entries in milliseconds (default: 5 minutes) */
  ttlMs?: number;
}

/**
 * Cache entry with timestamp for TTL support.
 */
interface CacheEntry<T> {
  value: T;
  timestamp: number;
}

/**
 * LRU cache with TTL support for detection results.
 */
export class DetectionCache<T> {
  private cache: Map<string, CacheEntry<T>> = new Map();
  private readonly maxEntries: number;
  private readonly ttlMs: number;

  constructor(options: DetectionCacheOptions = {}) {
    this.maxEntries = options.maxEntries ?? 1000;
    this.ttlMs = options.ttlMs ?? 5 * 60 * 1000; // 5 minutes default
  }

  /**
   * Get a cached value by key.
   * Returns undefined if not found or expired.
   */
  get(key: string): T | undefined {
    const entry = this.cache.get(key);
    if (!entry) {
      return undefined;
    }

    // Check TTL
    if (Date.now() - entry.timestamp > this.ttlMs) {
      this.cache.delete(key);
      return undefined;
    }

    // Move to end for LRU behavior
    this.cache.delete(key);
    this.cache.set(key, entry);

    return entry.value;
  }

  /**
   * Set a cached value.
   */
  set(key: string, value: T): void {
    // Evict oldest entries if at capacity
    if (this.cache.size >= this.maxEntries) {
      const firstKey = this.cache.keys().next().value;
      if (firstKey !== undefined) {
        this.cache.delete(firstKey);
      }
    }

    this.cache.set(key, {
      value,
      timestamp: Date.now(),
    });
  }

  /**
   * Check if a key exists and is not expired.
   */
  has(key: string): boolean {
    return this.get(key) !== undefined;
  }

  /**
   * Remove a specific key from the cache.
   */
  delete(key: string): boolean {
    return this.cache.delete(key);
  }

  /**
   * Clear all cached entries.
   */
  clear(): void {
    this.cache.clear();
  }

  /**
   * Get the number of cached entries.
   */
  get size(): number {
    return this.cache.size;
  }

  /**
   * Remove all expired entries.
   */
  prune(): number {
    const now = Date.now();
    let pruned = 0;
    const keysToDelete: string[] = [];

    this.cache.forEach((entry, key) => {
      if (now - entry.timestamp > this.ttlMs) {
        keysToDelete.push(key);
      }
    });

    for (const key of keysToDelete) {
      this.cache.delete(key);
      pruned++;
    }

    return pruned;
  }
}

/**
 * Generate a simple hash for content.
 * Used as a cache key to identify previously scanned content.
 *
 * Note: This is NOT cryptographically secure. It's designed for fast
 * content comparison, not security purposes.
 */
export function computeContentHash(content: string): string {
  let hash = 0;
  for (let i = 0; i < content.length; i++) {
    const char = content.charCodeAt(i);
    hash = (hash << 5) - hash + char;
    hash = hash & hash; // Convert to 32-bit integer
  }
  return Math.abs(hash).toString(36);
}

/**
 * Generate a cache key from content and optional context.
 */
export function generateCacheKey(content: string, context?: string): string {
  const contentHash = computeContentHash(content);
  if (context) {
    const contextHash = computeContentHash(context);
    return `${contentHash}:${contextHash}`;
  }
  return contentHash;
}
