/**
 * Privacy Manager for Claude Code Plugin.
 *
 * Coordinates privacy redaction across the plugin, providing a singleton
 * instance that integrates with the privacy-redaction package.
 *
 * Responsibilities:
 * - Initialize privacy service on session start
 * - Load configuration from user settings
 * - Provide redaction methods for extraction and queuing
 * - Track privacy statistics for debugging
 */

import {
  createPrivacyConfig,
  DEFAULT_PRIVACY_CONFIG,
  PrivacyService,
  type PrivacyConfig,
  type ProcessResult,
} from "@zest/privacy-redaction";
import { logger } from "../utils/logger.js";
import { createNodeFsAdapter } from "./node-fs-adapter.js";

/** Privacy configuration stored in user settings */
export interface PrivacySettings {
  /** How to handle detected sensitive data */
  approach: "detection" | "encryption" | "hybrid";
  /** Enable aggressive detection (more patterns, more false positives) */
  aggressiveMode: boolean;
  /** Respect .gitignore for file exclusion */
  enableGitignore: boolean;
  /** Respect .zest.rules for file exclusion */
  enableZestRules: boolean;
  /** Additional glob patterns for file exclusion */
  customExclusionPatterns: string[];
}

/** Default privacy settings */
export const DEFAULT_PRIVACY_SETTINGS: PrivacySettings = {
  approach: "detection",
  aggressiveMode: false,
  enableGitignore: true,
  enableZestRules: true,
  customExclusionPatterns: [],
};

/** Singleton instance of the privacy manager */
let privacyManagerInstance: PrivacyManager | null = null;

/**
 * Privacy Manager - coordinates privacy redaction for the Claude Code plugin.
 *
 * Use the singleton pattern via `getPrivacyManager()` to ensure consistent
 * privacy handling across the plugin.
 */
export class PrivacyManager {
  private service: PrivacyService | null = null;
  private initialized = false;
  private projectDir: string | undefined;

  /**
   * Initialize the privacy manager for a project.
   *
   * @param projectDir - The root directory of the current project
   * @param settings - Optional privacy settings (uses defaults if not provided)
   */
  async initialize(projectDir?: string, settings?: Partial<PrivacySettings>): Promise<void> {
    if (this.initialized && this.projectDir === projectDir) {
      logger.debug("Privacy manager already initialized for this project");
      return;
    }

    this.projectDir = projectDir;

    // Create config from settings
    const config = this.settingsToConfig(settings);

    // Create privacy service with Node.js file system adapter
    this.service = new PrivacyService({
      config,
      fs: createNodeFsAdapter(projectDir),
      enableCache: true,
    });

    // Initialize service (loads .gitignore and .zest.rules)
    await this.service.initialize();
    this.initialized = true;

    logger.debug("Privacy manager initialized", {
      projectDir,
      patternCount: this.service.getActivePatternCount(),
      exclusionCounts: this.service.getExclusionPatternCounts(),
    });
  }

  /**
   * Check if the privacy manager is initialized.
   */
  isInitialized(): boolean {
    return this.initialized && this.service !== null;
  }

  /**
   * Process content for sensitive data detection and redaction.
   *
   * @param content - The text content to process
   * @param filePath - Optional file path for context
   * @returns Processed result with redacted content, or original if not initialized
   */
  processContent(content: string, filePath?: string): ProcessResult {
    if (!this.service || !this.initialized) {
      // Return original content if not initialized
      return {
        content,
        detections: [],
        hasSensitiveData: false,
        wasRedacted: false,
      };
    }

    return this.service.processContent(content, filePath);
  }

  /**
   * Redact sensitive data from a message content string.
   *
   * @param content - The message content to redact
   * @returns Redacted content string
   */
  redactMessage(content: string): string {
    if (!this.service || !this.initialized) {
      return content;
    }

    const result = this.service.processContent(content);
    if (result.wasRedacted) {
      logger.debug("Redacted sensitive data from message", {
        detections: result.detections.length,
        patterns: result.detections.map((d) => d.pattern),
      });
    }
    return result.content;
  }

  /**
   * Redact sensitive data from a diff string.
   *
   * @param diff - The diff content to redact
   * @param filePath - Optional file path for context
   * @returns Redacted diff string
   */
  redactDiff(diff: string, filePath?: string): string {
    if (!this.service || !this.initialized) {
      return diff;
    }

    const result = this.service.processContent(diff, filePath);
    if (result.wasRedacted) {
      logger.debug("Redacted sensitive data from diff", {
        filePath,
        detections: result.detections.length,
      });
    }
    return result.content;
  }

  /**
   * Check if a file should be excluded from telemetry.
   *
   * @param filePath - Absolute path to the file
   * @returns True if file should be excluded
   */
  shouldExcludeFile(filePath: string): boolean {
    if (!this.service || !this.initialized) {
      return false;
    }

    const result = this.service.shouldExcludeFile(filePath);
    if (result.excluded) {
      logger.debug("File excluded from telemetry", {
        filePath,
        reason: result.reason,
        matchedRule: result.matchedRule,
      });
    }
    return result.excluded;
  }

  /**
   * Update privacy configuration.
   *
   * @param settings - New privacy settings
   */
  async updateSettings(settings: Partial<PrivacySettings>): Promise<void> {
    if (!this.service) {
      logger.warn("Cannot update settings: Privacy manager not initialized");
      return;
    }

    const config = this.settingsToConfig(settings);
    await this.service.updateConfig(config);

    logger.debug("Privacy settings updated", settings);
  }

  /**
   * Get privacy statistics.
   */
  getStats(): { totalDetections: number; filesProcessed: number; filesExcluded: number } | null {
    if (!this.service || !this.initialized) {
      return null;
    }

    const stats = this.service.getStats();
    return {
      totalDetections: stats.detection.totalDetections,
      filesProcessed: stats.filesProcessed,
      filesExcluded: stats.filesExcluded,
    };
  }

  /**
   * Clear caches and reset statistics.
   */
  reset(): void {
    if (this.service) {
      this.service.clearAll();
    }
    this.initialized = false;
    this.projectDir = undefined;
    logger.debug("Privacy manager reset");
  }

  /**
   * Convert user settings to privacy config format.
   */
  private settingsToConfig(settings?: Partial<PrivacySettings>): Partial<PrivacyConfig> {
    if (!settings) {
      return {};
    }

    return {
      approach: settings.approach,
      aggressiveMode: settings.aggressiveMode,
      enableGitignore: settings.enableGitignore,
      enableZestRules: settings.enableZestRules,
      customExclusionPatterns: settings.customExclusionPatterns,
    };
  }
}

/**
 * Get the singleton privacy manager instance.
 *
 * The manager must be initialized with `initialize()` before use.
 */
export function getPrivacyManager(): PrivacyManager {
  if (!privacyManagerInstance) {
    privacyManagerInstance = new PrivacyManager();
  }
  return privacyManagerInstance;
}

/**
 * Reset the singleton privacy manager instance.
 * Useful for testing or when project context changes.
 */
export function resetPrivacyManager(): void {
  if (privacyManagerInstance) {
    privacyManagerInstance.reset();
    privacyManagerInstance = null;
  }
}
