/**
 * Main redaction orchestrator.
 *
 * Combines detection results with redaction strategies to produce
 * redacted content with comprehensive metadata.
 */

import { isHighlySensitivePattern } from "../patterns";
import type {
  Detection,
  PrivacyApproach,
  PrivacyConfig,
  RedactionResult,
  RedactionStrategy,
} from "../types";
import { applyRedactionStrategy, encryptRedact } from "./strategies";

/**
 * Options for content redaction.
 */
export interface RedactContentOptions {
  /** Privacy approach to use (detection, encryption, or hybrid) */
  approach?: PrivacyApproach;
  /** Override encryption for highly sensitive data in hybrid mode */
  encryptHighlySensitive?: boolean;
}

/**
 * Redact sensitive data from content based on detections.
 *
 * This is the main redaction function that applies the appropriate
 * redaction strategy to each detection, handling position offsets
 * correctly by processing detections in reverse order.
 *
 * @param content - Original content with sensitive data
 * @param detections - Array of detections to redact
 * @param config - Privacy configuration
 * @param options - Additional redaction options
 * @returns Redaction result with redacted content and metadata
 *
 * @example
 * ```typescript
 * const result = redactContent(
 *   "API_KEY=sk-1234567890abcdef",
 *   [{ pattern: "generic_api_key", ... }],
 *   { approach: "detection", ... }
 * );
 * // result.redactedContent = "API_KEY=[REDACTED]"
 * ```
 */
export function redactContent(
  content: string,
  detections: Detection[],
  config: PrivacyConfig,
  options: RedactContentOptions = {},
): RedactionResult {
  const approach = options.approach ?? config.approach;
  const encryptHighlySensitive = options.encryptHighlySensitive ?? true;

  // No detections = no redaction needed
  if (detections.length === 0) {
    return {
      redactedContent: content,
      detections: [],
      stats: {
        totalDetections: 0,
        byPattern: {},
      },
      wasRedacted: false,
    };
  }

  // Sort detections by position (descending) to avoid offset issues during replacement
  const sortedDetections = [...detections].sort((a, b) => b.position.start - a.position.start);

  let redactedContent = content;
  const patternCounts: Record<string, number> = {};

  for (const detection of sortedDetections) {
    // Get the original text at the detection position
    const originalText = content.substring(detection.position.start, detection.position.end);

    // Determine the appropriate redaction strategy
    const strategy = determineStrategy(detection, approach, encryptHighlySensitive);

    // Apply the redaction
    const redactedText = applyRedactionStrategy(originalText, strategy);

    // Replace in content
    redactedContent =
      redactedContent.substring(0, detection.position.start) +
      redactedText +
      redactedContent.substring(detection.position.end);

    // Track pattern counts
    patternCounts[detection.pattern] = (patternCounts[detection.pattern] || 0) + 1;
  }

  return {
    redactedContent,
    detections,
    stats: {
      totalDetections: detections.length,
      byPattern: patternCounts,
    },
    wasRedacted: true,
  };
}

/**
 * Determine the redaction strategy for a detection based on privacy approach.
 *
 * @param detection - The detection to get strategy for
 * @param approach - The privacy approach being used
 * @param encryptHighlySensitive - Whether to encrypt highly sensitive data
 * @returns The redaction strategy to apply
 */
function determineStrategy(
  detection: Detection,
  approach: PrivacyApproach,
  encryptHighlySensitive: boolean,
): RedactionStrategy {
  switch (approach) {
    case "encryption":
      // Encrypt everything
      return "encrypt";

    case "hybrid":
      // Encrypt highly sensitive, use pattern default for others
      if (encryptHighlySensitive && isHighlySensitive(detection)) {
        return "encrypt";
      }
      return detection.redactionStrategy;

    case "detection":
    default:
      // Use the pattern's default strategy
      return detection.redactionStrategy;
  }
}

/**
 * Check if a detection is highly sensitive (warrants encryption).
 */
function isHighlySensitive(detection: Detection): boolean {
  return detection.highlySensitive || isHighlySensitivePattern(detection.pattern);
}

/**
 * Create a redaction summary from pattern counts.
 *
 * @param patternCounts - Map of pattern names to detection counts
 * @param patternDescriptions - Optional map of pattern names to descriptions
 * @returns Human-readable summary strings
 *
 * @example
 * ```typescript
 * createRedactionSummary({ "aws_access_key": 2, "generic_api_key": 1 })
 * // ["2 aws_access_keys", "1 generic_api_key"]
 * ```
 */
export function createRedactionSummary(
  patternCounts: Record<string, number>,
  patternDescriptions?: Record<string, string>,
): string[] {
  const summary: string[] = [];

  for (const [pattern, count] of Object.entries(patternCounts)) {
    const description = patternDescriptions?.[pattern] ?? pattern;
    const pluralized = count > 1 ? `${description}s` : description;
    summary.push(`${count} ${pluralized}`);
  }

  return summary;
}

/**
 * Batch redact multiple content strings.
 *
 * Useful when processing multiple files or chunks of content.
 *
 * @param items - Array of content/detection pairs
 * @param config - Privacy configuration
 * @param options - Additional redaction options
 * @returns Array of redaction results
 */
export function batchRedact(
  items: Array<{ content: string; detections: Detection[] }>,
  config: PrivacyConfig,
  options: RedactContentOptions = {},
): RedactionResult[] {
  return items.map((item) => redactContent(item.content, item.detections, config, options));
}

/**
 * Quick redaction helper that applies full redaction to all detections.
 *
 * @param content - Content to redact
 * @param detections - Detections to apply
 * @returns Redacted content string (no metadata)
 */
export function quickRedact(content: string, detections: Detection[]): string {
  if (detections.length === 0) return content;

  const sortedDetections = [...detections].sort((a, b) => b.position.start - a.position.start);

  let result = content;
  for (const detection of sortedDetections) {
    result =
      result.substring(0, detection.position.start) +
      "[REDACTED]" +
      result.substring(detection.position.end);
  }

  return result;
}

/**
 * Apply custom redaction function to detections.
 *
 * Allows callers to provide their own redaction logic.
 *
 * @param content - Content to redact
 * @param detections - Detections to process
 * @param redactFn - Custom function that takes matched text and returns replacement
 * @returns Redacted content string
 */
export function customRedact(
  content: string,
  detections: Detection[],
  redactFn: (matchedText: string, detection: Detection) => string,
): string {
  if (detections.length === 0) return content;

  const sortedDetections = [...detections].sort((a, b) => b.position.start - a.position.start);

  let result = content;
  for (const detection of sortedDetections) {
    const originalText = content.substring(detection.position.start, detection.position.end);
    const replacement = redactFn(originalText, detection);
    result =
      result.substring(0, detection.position.start) +
      replacement +
      result.substring(detection.position.end);
  }

  return result;
}
