/**
 * Built-in file exclusion rules.
 *
 * These patterns are always applied regardless of user configuration.
 * They cover sensitive files, build artifacts, binary files, and lock files.
 */

import type { BuiltInExclusionRule, ExclusionCategory } from "../types";

