/**
 * All built-in exclusion rules combined.
 */
export const ALL_BUILT_IN_RULES: BuiltInExclusionRule[] = [
  ...SENSITIVE_FILE_RULES,
  ...SENSITIVE_DIRECTORY_RULES,
  ...BUILD_ARTIFACT_RULES,
  ...LOCK_FILE_RULES,
  ...BINARY_MEDIA_RULES,
];

/**
 * Get all built-in patterns as a Set for fast lookup.
 */
export function getBuiltInPatterns(): Set<string> {
  return new Set(ALL_BUILT_IN_RULES.map((rule) => rule.pattern));
}

/**
 * Get built-in rules by category.
 */
export function getBuiltInRulesByCategory(category: ExclusionCategory): BuiltInExclusionRule[] {
  return ALL_BUILT_IN_RULES.filter((rule) => rule.category === category);
}

/**
 * Find the rule that matches a pattern.
 */
export function findBuiltInRule(pattern: string): BuiltInExclusionRule | undefined {
  return ALL_BUILT_IN_RULES.find((rule) => rule.pattern === pattern);
}

/**
 * Get statistics about built-in rules.
 */
export function getBuiltInRuleStats(): Record<ExclusionCategory, number> {
  const stats: Record<ExclusionCategory, number> = {
    sensitive_files: 0,
    sensitive_directories: 0,
    build_artifacts: 0,
    lock_files: 0,
    binary_media: 0,
  };

  for (const rule of ALL_BUILT_IN_RULES) {
    stats[rule.category]++;
  }

  return stats;
}
