/**
 * Sensitive data patterns for detecting various types of secrets, tokens, and credentials.
 *
 * Patterns are organized by category for better maintainability.
 * Each pattern includes metadata for proper handling during detection and redaction.
 */

import type { PatternCategory, SensitivePattern } from "../types";

/**
 * Helper to create a SensitivePattern with defaults.
 */
function createPattern(
  name: string,
  description: string,
  regex: RegExp,
  category: PatternCategory,
  options: {
    redactionStrategy?: "full" | "partial";
    aggressiveOnly?: boolean;
    highlySensitive?: boolean;
    priority?: number;
  } = {},
): SensitivePattern {
  return {
    name,
    description,
    regex,
    category,
    redactionStrategy: options.redactionStrategy ?? "full",
    aggressiveOnly: options.aggressiveOnly ?? false,
    highlySensitive: options.highlySensitive ?? false,
    priority: options.priority ?? 50,
  };
}

