/**
 * Pattern categorization and filtering utilities.
 *
 * Provides functions to filter, group, and query sensitive data patterns
 * based on various criteria like name, category, sensitivity level, etc.
 */

import type { PatternCategory, SensitivePattern } from "../types";
import {
  DEFAULT_PATTERN_NAMES,
  HIGHLY_SENSITIVE_PATTERN_NAMES,
  SENSITIVE_DATA_PATTERNS,
} from "./sensitive-patterns";

/**
 * Get all registered sensitive data patterns.
 */
export function getAllPatterns(): SensitivePattern[] {
  return SENSITIVE_DATA_PATTERNS;
}

/**
 * Get patterns filtered by name array.
 * @param names - Array of pattern names to include
 * @returns Patterns matching the provided names
 */
export function getPatternsByNames(names: readonly string[]): SensitivePattern[] {
  const nameSet = new Set(names);
  return SENSITIVE_DATA_PATTERNS.filter((pattern) => nameSet.has(pattern.name));
}

/**
 * Get default patterns (non-aggressive mode).
 * These are the most critical patterns that should always be detected.
 */
export function getDefaultPatterns(): SensitivePattern[] {
  return getPatternsByNames(DEFAULT_PATTERN_NAMES);
}

/**
 * Get all patterns including aggressive-only patterns.
 * Used when aggressive mode is enabled.
 */
export function getAllPatternsAggressive(): SensitivePattern[] {
  return SENSITIVE_DATA_PATTERNS;
}

/**
 * Get patterns that are NOT aggressive-only (safe for default mode).
 */
export function getNonAggressivePatterns(): SensitivePattern[] {
  return SENSITIVE_DATA_PATTERNS.filter((pattern) => !pattern.aggressiveOnly);
}

/**
 * Get highly sensitive patterns that should trigger encryption in hybrid mode.
 * These patterns contain data that, if exposed, could cause significant harm.
 */
export function getHighlySensitivePatterns(): SensitivePattern[] {
  return getPatternsByNames(HIGHLY_SENSITIVE_PATTERN_NAMES);
}

/**
 * Check if a pattern name is considered highly sensitive.
 * @param patternName - The pattern name to check
 */
export function isHighlySensitivePattern(patternName: string): boolean {
  return HIGHLY_SENSITIVE_PATTERN_NAMES.includes(patternName);
}

/**
 * Get patterns by category.
 * @param category - The pattern category to filter by
 */
export function getPatternsByCategory(category: PatternCategory): SensitivePattern[] {
  return SENSITIVE_DATA_PATTERNS.filter((pattern) => pattern.category === category);
}

/**
 * Get all unique pattern categories.
 */
export function getAllCategories(): PatternCategory[] {
  const categories = new Set<PatternCategory>();
  for (const pattern of SENSITIVE_DATA_PATTERNS) {
    categories.add(pattern.category);
  }
  return Array.from(categories);
}

/**
 * Get patterns grouped by category.
 * @returns Map of category to patterns in that category
 */
export function getPatternsByCategories(): Map<PatternCategory, SensitivePattern[]> {
  const grouped = new Map<PatternCategory, SensitivePattern[]>();

  for (const pattern of SENSITIVE_DATA_PATTERNS) {
    const existing = grouped.get(pattern.category) ?? [];
    existing.push(pattern);
    grouped.set(pattern.category, existing);
  }

  return grouped;
}

/**
 * Get a single pattern by name.
 * @param name - The pattern name to find
 * @returns The pattern or undefined if not found
 */
export function getPatternByName(name: string): SensitivePattern | undefined {
  return SENSITIVE_DATA_PATTERNS.find((pattern) => pattern.name === name);
}

/**
 * Get patterns sorted by priority (highest first).
 * Higher priority patterns should be preferred when there are overlapping matches.
 */
export function getPatternsSortedByPriority(): SensitivePattern[] {
  return [...SENSITIVE_DATA_PATTERNS].sort((a, b) => b.priority - a.priority);
}

/**
 * Get pattern statistics.
 */
export function getPatternStats(): {
  totalPatterns: number;
  byCategory: Record<PatternCategory, number>;
  defaultPatterns: number;
  aggressiveOnlyPatterns: number;
  highlySensitivePatterns: number;
} {
  const byCategory = {} as Record<PatternCategory, number>;

  for (const pattern of SENSITIVE_DATA_PATTERNS) {
    byCategory[pattern.category] = (byCategory[pattern.category] ?? 0) + 1;
  }

  return {
    totalPatterns: SENSITIVE_DATA_PATTERNS.length,
    byCategory,
    defaultPatterns: DEFAULT_PATTERN_NAMES.length,
    aggressiveOnlyPatterns: SENSITIVE_DATA_PATTERNS.filter((p) => p.aggressiveOnly).length,
    highlySensitivePatterns: HIGHLY_SENSITIVE_PATTERN_NAMES.length,
  };
}

/**
 * Select patterns based on privacy configuration.
 * @param aggressiveMode - Whether aggressive mode is enabled
 * @param enabledPatterns - Specific patterns to enable (empty = use defaults)
 */
export function selectPatterns(
  aggressiveMode: boolean,
  enabledPatterns: readonly string[],
): SensitivePattern[] {
  // If specific patterns are requested, use those
  if (enabledPatterns.length > 0) {
    return getPatternsByNames(enabledPatterns);
  }

  // Otherwise, use default or all patterns based on aggressive mode
  if (aggressiveMode) {
    return getAllPatterns();
  }

  return getNonAggressivePatterns();
}

/**
 * Get all pattern names.
 */
export function getAllPatternNames(): string[] {
  return SENSITIVE_DATA_PATTERNS.map((p) => p.name);
}

/**
 * Get default pattern names.
 */
export function getDefaultPatternNames(): readonly string[] {
  return DEFAULT_PATTERN_NAMES;
}

/**
 * Get highly sensitive pattern names.
 */
export function getHighlySensitivePatternNames(): readonly string[] {
  return HIGHLY_SENSITIVE_PATTERN_NAMES;
}
