/**
 * Redaction strategy implementations.
 *
 * Provides pure functions for different redaction approaches:
 * - Full redaction: Replace with [REDACTED]
 * - Partial redaction: Show first/last 20%, mask middle
 * - Hash redaction: Replace with [HASH:xxxx]
 * - Encrypt redaction: Replace with [ENCRYPTED:xxxx]
 *
 * All functions are pure and platform-agnostic.
 */

import type { RedactionStrategy } from "../types";

/**
 * Compute a simple non-cryptographic hash for content identification.
 * Used for hash redaction strategy.
 *
 * @param text - Text to hash
 * @returns 8-character hexadecimal hash
 */
export function simpleHash(text: string): string {
  let hash = 0;
  for (let i = 0; i < text.length; i++) {
    const char = text.charCodeAt(i);
    hash = (hash << 5) - hash + char;
    hash = hash & hash; // Convert to 32-bit integer
  }
  return Math.abs(hash).toString(16).padStart(8, "0").substring(0, 8);
}

/**
 * Full redaction - replace entire text with [REDACTED].
 *
 * @param _text - Text to redact (unused, replaced entirely)
 * @returns "[REDACTED]"
 *
 * @example
 * ```typescript
 * fullRedact("sk-1234567890abcdef") // "[REDACTED]"
 * ```
 */
export function fullRedact(_text: string): string {
  return "[REDACTED]";
}

/**
 * Partial redaction - show first and last 20% of text, mask the middle.
 *
 * For short strings (<=8 chars), falls back to full redaction.
 * Middle is replaced with asterisks, capped at 8 characters.
 *
 * @param text - Text to partially redact
 * @returns Partially redacted text with visible ends
 *
 * @example
 * ```typescript
 * partialRedact("sk-1234567890abcdef")
 * // "sk-1********cdef"
 *
 * partialRedact("short")
 * // "[REDACTED]" (too short for partial)
 * ```
 */
export function partialRedact(text: string): string {
  if (text.length <= 8) {
    return "[REDACTED]";
  }

  const visibleChars = Math.max(2, Math.floor(text.length * 0.2));
  const start = text.substring(0, visibleChars);
  const end = text.substring(text.length - visibleChars);
  const middleLength = text.length - 2 * visibleChars;

  // Cap the middle asterisks at 8 for readability
  return `${start}${"*".repeat(Math.min(middleLength, 8))}${end}`;
}

/**
 * Hash redaction - replace with a hash identifier.
 *
 * Useful when you need to identify recurring values without exposing them.
 *
 * @param text - Text to hash-redact
 * @returns "[HASH:xxxxxxxx]" where xxxxxxxx is an 8-char hex hash
 *
 * @example
 * ```typescript
 * hashRedact("sk-1234567890")
 * // "[HASH:a1b2c3d4]"
 * ```
 */
export function hashRedact(text: string): string {
  return `[HASH:${simpleHash(text)}]`;
}

/**
 * Encrypt redaction - replace with encrypted marker and hash.
 *
 * Note: This is a simplified implementation using hashing rather than
 * actual encryption. Production usage should integrate with proper
 * cryptographic key management.
 *
 * The format includes a preview of the encrypted output and a content hash
 * for identification purposes.
 *
 * @param text - Text to encrypt-redact
 * @returns "[ENCRYPTED:xxxx...yyyy]" format
 *
 * @example
 * ```typescript
 * encryptRedact("secret-api-key")
 * // "[ENCRYPTED:a1b2c3d4...e5f6g7h8]"
 * ```
 */
export function encryptRedact(text: string): string {
  // Create a content hash for identification
  const contentHash = simpleHash(text);

  // Create a secondary hash for the "encrypted" portion
  // In production, this would use actual AES-256 encryption
  const encryptedHash = simpleHash(text + "salt");

  return `[ENCRYPTED:${encryptedHash}...${contentHash}]`;
}

/**
 * Apply the specified redaction strategy to text.
 *
 * @param text - Text to redact
 * @param strategy - Redaction strategy to apply
 * @returns Redacted text
 *
 * @example
 * ```typescript
 * applyRedactionStrategy("sk-123456", "full")    // "[REDACTED]"
 * applyRedactionStrategy("sk-123456", "partial") // "sk********56"
 * applyRedactionStrategy("sk-123456", "hash")    // "[HASH:a1b2c3d4]"
 * applyRedactionStrategy("sk-123456", "encrypt") // "[ENCRYPTED:...]"
 * ```
 */
export function applyRedactionStrategy(text: string, strategy: RedactionStrategy): string {
  switch (strategy) {
    case "full":
      return fullRedact(text);
    case "partial":
      return partialRedact(text);
    case "hash":
      return hashRedact(text);
    case "encrypt":
      return encryptRedact(text);
    default:
      // Default to full redaction for unknown strategies
      return fullRedact(text);
  }
}

/**
 * Strategy metadata for UI/documentation purposes.
 */
export interface RedactionStrategyInfo {
  /** Strategy identifier */
  name: RedactionStrategy;
  /** Human-readable label */
  label: string;
  /** Description of how this strategy works */
  description: string;
  /** Whether this strategy is reversible */
  reversible: boolean;
  /** Whether this strategy preserves format/length hints */
  preservesFormat: boolean;
}

/**
 * Information about all available redaction strategies.
 */
export const REDACTION_STRATEGIES: RedactionStrategyInfo[] = [
  {
    name: "full",
    label: "Full Redaction",
    description: "Replace sensitive data entirely with [REDACTED]",
    reversible: false,
    preservesFormat: false,
  },
  {
    name: "partial",
    label: "Partial Redaction",
    description: "Show first and last 20% of text, mask the middle with asterisks",
    reversible: false,
    preservesFormat: true,
  },
  {
    name: "hash",
    label: "Hash Redaction",
    description: "Replace with a hash identifier for tracking recurring values",
    reversible: false,
    preservesFormat: false,
  },
  {
    name: "encrypt",
    label: "Encrypted Redaction",
    description: "Replace with encrypted marker (requires key management for recovery)",
    reversible: true,
    preservesFormat: false,
  },
];

/**
 * Get strategy info by name.
 */
export function getStrategyInfo(strategy: RedactionStrategy): RedactionStrategyInfo | undefined {
  return REDACTION_STRATEGIES.find((s) => s.name === strategy);
}
